.fullScreen {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgb(88, 86, 86, 0.7);
    top: 0;
    left: 0;
    visibility: hidden;
    z-index: 1000;
}
.modalWindow {
    display: flex;
    flex-direction: column;
    width: 18vw;
    height: 15rem;
    position: fixed;
    top: 25vh;
    left: 30vw;
    border: 5px solid rgb(103, 21, 210);
    border-radius: 1vw;
    padding: 0.5VW;
    background-color: rgb(13, 12, 12);
    color: white;
    visibility: hidden;
    h3 {
        margin-top: 1vw;
        margin-left: 1.5vw;
    }
    span {
        display: inline-block;
        width: 10vw;
    }
}

.boxField {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    span {
        display: inline-block;
        width: 10vw;
    }
}

.name {
    margin: 0 1vw;
    height: 1.5rem;
    width: 10vw;
    display: inline;
    border-radius: 0.3vw;
}

.comment {
    margin-top: 1vw;
    height: 5rem;
    width: 20vw;
    border-radius: 0.3vw;
}

.add {
    margin-top: 1vw;
    width: 5vw;
    height: 3rem;
    position: relative;
    left: 13vw;
    cursor: pointer;
    border-radius: 0.5vw;
    background-color:rgb(103, 21, 210);
    color: white;

}

.close {
    width: 1vw;
    height: 1.1vw;
    padding: 0;
    position: relative;
    left: 17vw;
    cursor: pointer;
}
.square {
    width: 1vw;
    height: 1vw;
}
.inputColor {
    display: flex;
    width: 5vw;
    height: 1rem;
    margin-top: 0.5vw;
}
@media only screen and (max-width: 1000px) {
    .modalWindow{
        width: 95vw;
        height: 60vh;
        position: fixed;
        top: 10vh;
        left: 2vw;
    }
    .boxField {
        span {
            width: 40vw;
        }
    }
    .name {
        height: 1.5rem;
        width: 70vw;
    }
    .add {
        position: fixed;
        width: 30vw;
        height: 3rem;
        left: 13vw;
        top: 60vh;
    }
    .close {
        width: 10vw;
        height: 11vw;
        left: 85vw;
    }
    .square {
        width: 10vw;
        height: 3vh;
    }

    .inputColor {
        display: flex;
        width: 20vw;
        height: 1rem;
        margin: 3vw;
    }
    
}