.container {
    width: 50vw;
    height: 90vh;
    text-align: center;
}

.backup {
    width: 40vw;
    height: 45vh;
    margin: 10vh 20vw;
    border-radius: 0.5vw;
    background-color: rgb(103, 21, 210);
    color: white;
    padding: 0.5vh 1vw;
    position: relative;
    left: 15vw
}
.buttonSend, .buttonLoad  {
    background-color: rgb(13, 12, 12);
    cursor: pointer;
    margin: 2vh 18vw;
    height: 6vh;
    color: white;
    text-align: center;
    border-radius: 0.5vw;
    &:hover {
        background-color: rgb(32, 27, 33);
    }
}

@media only screen and (max-width: 1000px) {
    .container {
        width: 80vw;
        height: 60vh;
    }

    .backup {
        width: 70vw;
        height: 50vh;
        border-radius: 3vw;
        position: fixed;
        left: -5vw;
        top: 20vh;
    }
}
